            transform: translate3d(0, 40px, 0);
  }
  100% {
    opacity: 1;
  }
}

.enter-transition {
  -webkit-transition: opacity 2s, color .2s, background-color .2s;
  transition: opacity 2s, color .2s, background-color .2s;
}

.enter-enter, .enter-leave {
  opacity: 0;
}

body {
  font-family: "Helvetica Neue", "Microsoft Yahei", sans-serif;
}

.page-home{}

.page-home-jumbotron{
  background-color: #ecf6fb;
  padding-bottom: 30px;
  position: relative;
  text-align: center;
}

.page-home-lang{
  margin-top: 20px;
  height: 16px;
}

.page-home-logo{
  height: 262px;
  padding-top: 40px;
  opacity: 0;
}

.page-home-logo img {
  width: 150px;
}

.page-home-logo.logo-animate{
  -webkit-animation: enter .5s forwards;
          animation: enter .5s forwards;
}

.page-home-title{
  margin-top: 10px;
  font-size: 30px;
  color: #26a2ff;
}

.page-home-desc{
  margin-top: 10px;
  font-size: 20px;
  color: #26a2ff;
  opacity: 0;
}

.page-home-desc.logo-animate{
  -webkit-animation: enter .5s forwards;
          animation: enter .5s forwards;
}

.page-home-buttons{
  margin: 30px 0;
  height: 46px;
}

.page-home-button{
  display: inline-block;
  color: #26a2ff;
  padding: 10px;
  width: 200px;
  border: solid 2px #26a2ff;
  border-radius: 5px;
  font-size: 18px;
}

.page-home-button:first-child{
  margin-right: 40px;
}

.page-home-button:hover{
  color: #fff;
  background-color: #26a2ff;
}

.page-home-gitbtn{
  height: 25px;
  margin-top: 20px;
}

.page-home-qrcode{
  text-align: center;
  margin-top: 40px;
}

.page-home-qrcode img {
  width: 250px;
}

.page-home-sector{
  margin-top: 40px;
  font-size: 20px;
  color: #26a2ff;
  text-align: center;
}

.page-home-sector--dark{
  padding-top: 20px;
}

.page-home-start{
  margin: 40px auto 60px;
  background-color: #f8f8f8;
  border-radius: 8px;
  border: solid 1px #ddd;
  padding: 15px;
}

.page-home-start code {
  display: block
}

.page-home-start code:first-child{
  margin-bottom: 25px;
}

.page-home-start code > span{
  display: block;
  line-height: 20px;
}

.page-home-start code > span.page-home-comment{
  color: #969896;
}

.page-home-start .pl-smi {
  color: #333;
}

.page-home-start .pl-k {
  color: #a71d5d;
}

.page-home-start .pl-s {
  color: #183691;
}

.page-home-start .pl-en {
  color: #795da3;
}

.page-home-show{
  margin: 0 auto;
  padding: 40px 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.page-home-left{
  margin-right: 40px;
}

.page-home-feature{
  display: inline-block;
}

.page-home-feature li {
  color: #333;
  margin-bottom: 30px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.page-home-feature i {
  vertical-align: middle;
  color: #26a2ff;
  font-size: 80px;
  display: -webkit-box;
}

.page-home-feature span {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  padding-left: 20px;
  line-height: 1.6;
  vertical-align: middle;
  font-size: 16px;
  display: -webkit-box;
}

.page-home-phone{
  min-width: 350px;
  display: inline-block;
  width: 350px;
  height: 760px;
  background: url('./assets/phone.png') no-repeat;
  background-size: cover;
  padding: 100px 12px 100px;
}

.page-home-phone iframe {
  width: 100%;
  height: 100%;
}

.page-home-footer{
  margin: 60px 0 20px;
  text-align: center;
}

.page-home-footer a {
  color: #26a2ff;
}
@media (max-width: 500px) {
  html {
    font-size: 37.5%;
  }
  .page-home{}
  .page-home-jumbotron{
    padding-bottom: 30px;
    position: relative;
  }
  .page-home-logo{
    height: auto;
  }
  .page-home-content{
    position: absolute;
    width: 100%;
  }
  .page-home-title{
    font-size: 24px;
  }
  .page-home-desc{
    font-size: 18px;
  }
  .page-home-buttons{
    margin: 30px auto;
    width: 90%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
  }
  .page-home-button{
    display: -webkit-box;
    line-height: 24px;
    padding: 10px 0;
    width: 45%;
    text-align: center;
    font-size: 14px;
  }
  .page-home-button span {
    display: block;
    width: 100%;
    text-align: center;
  }
  .page-home-button:first-child{
    margin-right: 0;
  }
  .page-home-left{
    margin-right: 0;
    width: 90%;
  }
  .page-home-feature span {
    font-size: 14px;
  }
  .page-home-qrcode{
    display: none;
  }
  .page-home-start{
    width: 90%;
  }
  .page-home-phone{
    display: none;
  }
}



